import { GeoService, type GeolocationData } from "./geo-service"

declare global {
  interface Navigator {
    deviceMemory?: number;
    connection?: {
      type?: string;
      effectiveType?: string;
    };
    getBattery?: () => Promise<{
      level: number;
    }>;
  }
}

interface DeviceInfo {
  userAgent: string
  platform: string
  screenResolution: string
  ipAddress: string
  ipCountry: string | null
  ipCity: string | null
  browserInfo: string
  deviceFingerprint: string
  networkType: string
  batteryLevel?: number
  deviceMemory?: number
  hardwareConcurrency: number
  language: string
  timezone: string
  vendor: string
}

export interface TrackingLink {
  id: string
  shortCode: string
  templateType: string
  isActive: boolean
  clicks: number
  captures: number
  createdAt: string
  expiresAt: string
}

export interface LocationCapture {
  id: string
  linkId: string
  latitude: number
  longitude: number
  accuracy: number
  altitude: number | null
  speed: number | null
  heading: number | null
  city: string | null
  country: string | null
  address: string | null
  capturedAt: string
  deviceInfo: DeviceInfo
}

interface IPInfo {
  ip: string
  country: string | null
  city: string | null
  latitude?: number
  longitude?: number
}

export class TrackingService {
  private static readonly STORAGE_KEY = 'tracking_data'
  private static readonly API_BASE = "/api"
  private static readonly MAX_RETRIES = 3
  private static readonly RETRY_DELAY = 1000 // 1 second

  private static async getIPInfo(): Promise<IPInfo> {
    try {
      const services = [
        {
          url: 'https://ipapi.co/json/',
          transform: (data: any): IPInfo => ({
            ip: data.ip,
            country: data.country_name,
            city: data.city,
            latitude: data.latitude,
            longitude: data.longitude
          })
        },
        {
          url: 'https://ipinfo.io/json',
          transform: (data: any): IPInfo => ({
            ip: data.ip,
            country: data.country,
            city: data.city,
            latitude: data.loc ? parseFloat(data.loc.split(',')[0]) : undefined,
            longitude: data.loc ? parseFloat(data.loc.split(',')[1]) : undefined
          })
        }
      ]

      for (const service of services) {
        for (let attempt = 0; attempt < this.MAX_RETRIES; attempt++) {
          try {
            const response = await fetch(service.url)
            if (response.ok) {
              const data = await response.json()
              return service.transform(data)
            }
          } catch (e) {
            if (attempt === this.MAX_RETRIES - 1) continue
            await new Promise(resolve => setTimeout(resolve, this.RETRY_DELAY * (attempt + 1)))
          }
        }
      }

      return {
        ip: await this.getFallbackIP(),
        country: null,
        city: null
      }
    } catch (error) {
      console.error('Error getting IP information:', error)
      return {
        ip: await this.getFallbackIP(),
        country: null,
        city: null
      }
    }
  }

  private static async getFallbackIP(): Promise<string> {
    try {
      const response = await fetch('https://api64.ipify.org?format=json')
      const data = await response.json()
      return data.ip
    } catch {
      return 'unknown'
    }
  }

  private static async getDeviceInfo(): Promise<DeviceInfo> {
    const baseInfo: DeviceInfo = {
      userAgent: navigator.userAgent,
      platform: navigator.platform,
      screenResolution: `${window.screen.width}x${window.screen.height}`,
      ipAddress: "0.0.0.0",
      ipCountry: null,
      ipCity: null,
      browserInfo: this.getBrowserInfo(),
      deviceFingerprint: await this.generateDeviceFingerprint(),
      networkType: "unknown",
      hardwareConcurrency: navigator.hardwareConcurrency,
      language: navigator.language,
      timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
      vendor: navigator.vendor
    }

    try {
      const connection = (navigator as any).connection
      if (connection) {
        baseInfo.networkType = connection.type || "unknown"
      }

      const battery = await (navigator as any).getBattery?.()
      if (battery) {
        baseInfo.batteryLevel = Math.round(battery.level * 100)
      }

      if ((navigator as any).deviceMemory) {
        baseInfo.deviceMemory = (navigator as any).deviceMemory
      }
    } catch (e) {
      console.warn("Some device APIs not available:", e)
    }

    return baseInfo
  }

  private static getBrowserInfo(): string {
    const ua = navigator.userAgent
    const browserRegex = /(chrome|safari|firefox|edge|opera|ie)\/([\d.]+)/i
    const match = ua.match(browserRegex)
    return match 
      ? `${match[1].charAt(0).toUpperCase() + match[1].slice(1)} ${match[2]}`
      : "Unknown Browser"
  }

  private static async generateDeviceFingerprint(): Promise<string> {
    const components = [
      navigator.userAgent,
      navigator.language,
      new Date().getTimezoneOffset(),
      screen.width,
      screen.height,
      screen.colorDepth,
      navigator.hardwareConcurrency,
      navigator.platform,
      (navigator as any).deviceMemory,
      (navigator as any).connection?.type
    ]
    
    const fingerprint = components.join('|')
    const encoder = new TextEncoder()
    const data = encoder.encode(fingerprint)
    const hashBuffer = await crypto.subtle.digest('SHA-256', data)
    const hashArray = Array.from(new Uint8Array(hashBuffer))
    return hashArray.map(b => b.toString(16).padStart(2, '0')).join('')
  }

  private static async getStoredData(): Promise<{
    links: TrackingLink[]
    captures: LocationCapture[]
  }> {
    try {
      const data = localStorage.getItem(this.STORAGE_KEY)
      if (data) {
        return JSON.parse(data)
      }
    } catch (error) {
      console.error("Error reading stored data:", error)
    }
    return { links: [], captures: [] }
  }

  private static async saveData(data: { links: TrackingLink[], captures: LocationCapture[] }): Promise<void> {
    try {
      localStorage.setItem(this.STORAGE_KEY, JSON.stringify(data))
    } catch (error) {
      console.error("Error saving data:", error)
    }
  }

  static async createTrackingLink(templateType: string): Promise<TrackingLink> {
    try {
      const response = await fetch(`${this.API_BASE}/tracking-links`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ templateType })
      })

      if (!response.ok) {
        throw new Error("Failed to create tracking link: Server returned an error")
      }

      const newLink = await response.json()
      const data = await this.getStoredData()
      data.links.push(newLink)
      await this.saveData(data)

      return newLink
    } catch (error) {
      console.error("Error creating tracking link:", error)
      throw error
    }
  }

  static async getTrackingLinks(): Promise<TrackingLink[]> {
    try {
      const data = await this.getStoredData()
      return data.links
    } catch (error) {
      console.error("Error fetching tracking links:", error)
      throw error
    }
  }

  static async getLocationCaptures(): Promise<LocationCapture[]> {
    try {
      const data = await this.getStoredData()
      return data.captures
    } catch (error) {
      console.error("Error fetching location captures:", error)
      throw error
    }
  }

  static async captureLocation(linkId: string, shortCode: string): Promise<LocationCapture> {
    try {
      // Try to get high-precision GPS location
      const position = await GeoService.getCurrentPosition({
        enableHighAccuracy: true,
        timeout: 30000,
        maximumAge: 0
      })

      // Get device information and location details
      const [deviceInfo, locationDetails, ipInfo] = await Promise.all([
        this.getDeviceInfo(),
        GeoService.getLocationDetails(position.latitude, position.longitude),
        this.getIPInfo()
      ])

      // Create capture with all available data
      const capture: LocationCapture = {
        id: `capture-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
        linkId,
        latitude: position.latitude,
        longitude: position.longitude,
        accuracy: position.accuracy,
        altitude: position.altitude,
        speed: position.speed,
        heading: position.heading,
        city: locationDetails.city || null,
        country: locationDetails.country || null,
        address: locationDetails.address || null,
        capturedAt: new Date().toISOString(),
        deviceInfo: {
          ...deviceInfo,
          ipAddress: ipInfo.ip,
          ipCountry: ipInfo.country,
          ipCity: ipInfo.city
        }
      }

      // Send data to server
      const response = await fetch(`${this.API_BASE}/tracking-captures`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(capture)
      })

      if (!response.ok) {
        throw new Error('Failed to save capture data')
      }

      // Save to local storage
      const data = await this.getStoredData()
      data.captures.push(capture)
      
      // Update tracking link statistics
      const linkIndex = data.links.findIndex(l => l.id === linkId)
      if (linkIndex !== -1) {
        data.links[linkIndex].captures++
      }
      
      await this.saveData(data)
      return capture
    } catch (error) {
      console.error("Error capturing location:", error)
      throw error
    }
  }
}
