"use client"

import { useState } from "react"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { CreateLink, CreateLinkButton } from "./create-link"
import { type TrackingLink } from "@/lib/db/schema"

export function CreateLinkDialog() {
  const [isOpen, setIsOpen] = useState(false)
  const [createdLink, setCreatedLink] = useState<TrackingLink | null>(null)
  const [copiedType, setCopiedType] = useState<'masked' | 'tracking' | null>(null)

  const generateMaskedUrl = (templateType: string, shortCode: string) => {
    const baseUrl = window.location.origin
    return `${baseUrl}/share/${shortCode}`
  }

  const generateTrackingUrl = (shortCode: string) => {
    const baseUrl = window.location.origin
    return `${baseUrl}/track/${shortCode}`
  }

  const handleCopy = async (text: string, type: 'masked' | 'tracking') => {
    try {
      await navigator.clipboard.writeText(text)
      setCopiedType(type)
      setTimeout(() => setCopiedType(null), 2000)
    } catch (err) {
      console.error("Error al copiar:", err)
    }
  }

  const handleShare = async (url: string, title: string) => {
    try {
      if (navigator.share) {
        await navigator.share({
          title: title,
          text: "Verifica tu ubicaci√≥n",
          url: url
        })
      } else {
        throw new Error("Compartir no soportado")
      }
    } catch (err) {
      console.error("Error al compartir:", err)
      // Si falla compartir, intentamos copiar al portapapeles
      await navigator.clipboard.writeText(url)
    }
  }

  const handleLinkCreated = (link: TrackingLink) => {
    setCreatedLink(link)
  }

  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogTrigger asChild>
        <CreateLinkButton />
      </DialogTrigger>
      <DialogContent className="sm:max-w-[800px] max-h-[80vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>
            {createdLink ? (
              <>
                <span className="mr-2">‚ú®</span>
                Enlace Creado Exitosamente
              </>
            ) : (
              <>
                <span className="mr-2">üéØ</span>
                Crear Nuevo Enlace de Rastreo
              </>
            )}
          </DialogTitle>
        </DialogHeader>

        {createdLink ? (
          <div className="space-y-6">
            {/* URL Enmascarada */}
            <div className="space-y-2">
              <div className="text-sm font-medium">URL Enmascarada</div>
              <div className="flex items-center gap-2">
                <code className="flex-1 p-2 rounded bg-gray-900/50 text-sm break-all">
                  {generateMaskedUrl(createdLink.templateType, createdLink.shortCode)}
                </code>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => handleCopy(
                    generateMaskedUrl(createdLink.templateType, createdLink.shortCode),
                    'masked'
                  )}
                >
                  {copiedType === 'masked' ? (
                    <span className="text-green-500">‚úì Copiado</span>
                  ) : (
                    <>
                      <span className="mr-2">üìã</span>
                      Copiar
                    </>
                  )}
                </Button>
              </div>
            </div>

            {/* URL de Rastreo */}
            <div className="space-y-2">
              <div className="text-sm font-medium">URL de Rastreo</div>
              <div className="flex items-center gap-2">
                <code className="flex-1 p-2 rounded bg-gray-900/50 text-sm break-all">
                  {generateTrackingUrl(createdLink.shortCode)}
                </code>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => handleCopy(
                    generateTrackingUrl(createdLink.shortCode),
                    'tracking'
                  )}
                >
                  {copiedType === 'tracking' ? (
                    <span className="text-green-500">‚úì Copiado</span>
                  ) : (
                    <>
                      <span className="mr-2">üìã</span>
                      Copiar
                    </>
                  )}
                </Button>
              </div>
            </div>

            {/* Botones de Compartir */}
            <div className="flex flex-wrap gap-2">
              <Button
                variant="outline"
                onClick={() => handleShare(
                  generateMaskedUrl(createdLink.templateType, createdLink.shortCode),
                  "Verificaci√≥n de Ubicaci√≥n"
                )}
              >
                <span className="mr-2">üì±</span>
                Compartir
              </Button>
              <Button
                variant="outline"
                onClick={() => window.open(
                  `https://wa.me/?text=${encodeURIComponent(
                    generateMaskedUrl(createdLink.templateType, createdLink.shortCode)
                  )}`,
                  '_blank'
                )}
              >
                <span className="mr-2">üí¨</span>
                WhatsApp
              </Button>
              <Button
                variant="outline"
                onClick={() => window.open(
                  `https://t.me/share/url?url=${encodeURIComponent(
                    generateMaskedUrl(createdLink.templateType, createdLink.shortCode)
                  )}`,
                  '_blank'
                )}
              >
                <span className="mr-2">‚úàÔ∏è</span>
                Telegram
              </Button>
            </div>

            {/* Crear Otro Enlace */}
            <div className="flex justify-end">
              <Button
                variant="default"
                onClick={() => setCreatedLink(null)}
              >
                <span className="mr-2">‚ûï</span>
                Crear Otro Enlace
              </Button>
            </div>
          </div>
        ) : (
          <CreateLink onLinkCreated={handleLinkCreated} />
        )}
      </DialogContent>
    </Dialog>
  )
}
