"use client"

import { useEffect, useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import LiveLocationMap from "@/components/map/live-location-map"
import { TrackingService } from "@/lib/services/tracking-service"
import { type GeolocationData } from "@/lib/services/geo-service"

interface LocationTrackerProps {
  linkId?: string
  shortCode?: string
  onLocationCaptured?: () => void
}

export default function LocationTracker({ linkId, shortCode, onLocationCaptured }: LocationTrackerProps) {
  const [isCapturing, setIsCapturing] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [location, setLocation] = useState<GeolocationData | null>(null)

  const handleCapture = async () => {
    try {
      setIsCapturing(true)
      setError(null)

      // Obtener ubicaci√≥n
      const position = await getCurrentPosition()
      const locationData: GeolocationData = {
        latitude: position.coords.latitude,
        longitude: position.coords.longitude,
        accuracy: position.coords.accuracy,
        altitude: position.coords.altitude,
        speed: position.coords.speed,
        heading: position.coords.heading,
        timestamp: position.timestamp
      }

      setLocation(locationData)

      // Enviar datos al servidor solo si tenemos linkId y shortCode
      if (linkId && shortCode) {
        await TrackingService.captureLocation(linkId, shortCode)
      }

      if (onLocationCaptured) {
        onLocationCaptured()
      }
    } catch (err) {
      console.error("Error capturing location:", err)
      setError(getErrorMessage(err))
    } finally {
      setIsCapturing(false)
    }
  }

  return (
    <Card className="bg-black/40 backdrop-blur-sm border-gray-800">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <span>üìç</span>
          <span>Verificaci√≥n de Ubicaci√≥n</span>
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-6">
        {error ? (
          <div className="bg-red-500/10 border border-red-500/50 rounded-lg p-4 text-red-400">
            {error}
          </div>
        ) : location ? (
          <>
            <div className="h-[300px] rounded-lg overflow-hidden">
              <LiveLocationMap
                height="300px"
                showAccuracyCircle={true}
                initialLocation={location}
              />
            </div>
            <div className="text-center text-green-400">
              ‚úì Ubicaci√≥n capturada exitosamente
            </div>
          </>
        ) : (
          <div className="text-center space-y-4">
            <p className="text-muted-foreground">
              Para continuar, necesitamos verificar tu ubicaci√≥n actual.
            </p>
            <Button
              onClick={handleCapture}
              disabled={isCapturing}
              className="bg-gradient-to-r from-blue-600 to-blue-800 hover:from-blue-700 hover:to-blue-900"
            >
              {isCapturing ? (
                <>
                  <div className="animate-spin rounded-full h-4 w-4 border-2 border-current mr-2" />
                  Obteniendo ubicaci√≥n...
                </>
              ) : (
                <>
                  <span className="mr-2">üìç</span>
                  Verificar Ubicaci√≥n
                </>
              )}
            </Button>
          </div>
        )}
      </CardContent>
    </Card>
  )
}

// Funciones auxiliares
async function getCurrentPosition(): Promise<GeolocationPosition> {
  return new Promise((resolve, reject) => {
    if (!navigator.geolocation) {
      reject(new Error("Geolocalizaci√≥n no soportada"))
      return
    }

    const options = {
      enableHighAccuracy: true,
      timeout: 5000,
      maximumAge: 0
    }

    navigator.geolocation.getCurrentPosition(
      resolve,
      (error) => {
        reject(new Error(getGeolocationErrorMessage(error)))
      },
      options
    )
  })
}

function getGeolocationErrorMessage(error: GeolocationPositionError): string {
  switch (error.code) {
    case error.PERMISSION_DENIED:
      return "Acceso a la ubicaci√≥n denegado. Por favor, permite el acceso a tu ubicaci√≥n."
    case error.POSITION_UNAVAILABLE:
      return "No se pudo obtener tu ubicaci√≥n. Por favor, verifica que los servicios de ubicaci√≥n est√©n activados."
    case error.TIMEOUT:
      return "Se agot√≥ el tiempo para obtener tu ubicaci√≥n. Por favor, intenta nuevamente."
    default:
      return "Error al obtener la ubicaci√≥n."
  }
}

function getErrorMessage(error: unknown): string {
  if (error instanceof Error) {
    return error.message
  }
  return "Error desconocido al capturar la ubicaci√≥n"
}

export { LocationTracker }
