"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"
import { type TrackingLink } from "@/lib/db/schema"
import { templates, getTemplateUrgencyColor, getTemplateBgColor } from "@/config/templates"

interface CreateLinkProps {
  onLinkCreated?: (link: TrackingLink) => void
}

export function CreateLinkButton({ onClick }: { onClick?: () => void }) {
  return (
    <Button
      onClick={onClick}
      className="bg-gradient-to-r from-blue-600 to-blue-800 hover:from-blue-700 hover:to-blue-900"
    >
      <span className="mr-2">üéØ</span>
      Crear Nuevo Enlace
    </Button>
  )
}

export function CreateLink({ onLinkCreated }: CreateLinkProps) {
  const [selectedTemplate, setSelectedTemplate] = useState<string | null>(null)
  const [isCreating, setIsCreating] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const handleCreateLink = async (templateType: string) => {
    try {
      setIsCreating(true)
      setError(null)
      
      const response = await fetch('/api/tracking-links', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ templateType })
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || 'Error al crear el enlace')
      }

      if (onLinkCreated) {
        onLinkCreated(data.link)
      }
      setSelectedTemplate(null)
    } catch (err) {
      console.error("Error al crear enlace:", err)
      setError("Error al crear el enlace. Por favor, intenta nuevamente.")
    } finally {
      setIsCreating(false)
    }
  }

  return (
    <div className="space-y-6">
      {error && (
        <div className="bg-red-500/10 border border-red-500/50 rounded-lg p-4 text-red-400">
          {error}
        </div>
      )}

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4 max-h-[60vh] overflow-y-auto pr-4">
        {Object.entries(templates).map(([type, template]) => (
          <Card
            key={type}
            className={`
              relative overflow-hidden cursor-pointer transition-all duration-200
              ${selectedTemplate === type 
                ? 'ring-2 ring-blue-500 bg-blue-950/30' 
                : 'hover:bg-gray-900/50 bg-black/40'}
              backdrop-blur-sm border-gray-800
            `}
            onClick={() => setSelectedTemplate(type)}
          >
            <CardContent className="p-6">
              <div className="text-3xl mb-4">
                {template.icon}
              </div>
              <h3 className="text-lg font-semibold mb-2">
                {template.title}
              </h3>
              <p className="text-sm text-muted-foreground mb-4">
                {template.subtitle}
              </p>

              {/* Detalles de Urgencia y Validez */}
              <div className="space-y-2 mb-4">
                <div className={`text-xs ${getTemplateUrgencyColor(template.urgencyLevel)}`}>
                  {template.urgencyLevel === 'high' ? '‚ö†Ô∏è Alta Urgencia' : 
                   template.urgencyLevel === 'medium' ? '‚ö° Urgencia Media' : 
                   'üìù Normal'}
                </div>
                <div className="text-xs text-yellow-400">
                  ‚è±Ô∏è V√°lido por: {template.validityPeriod}
                </div>
              </div>

              {/* Descripci√≥n de la plantilla */}
              <div className="text-xs text-muted-foreground mb-4">
                {template.description}
              </div>

              {selectedTemplate === type && (
                <Button
                  className={`w-full bg-gradient-to-r ${getTemplateBgColor(type)} hover:opacity-90`}
                  disabled={isCreating}
                  onClick={(e) => {
                    e.stopPropagation()
                    handleCreateLink(type)
                  }}
                >
                  {isCreating ? (
                    <>
                      <div className="animate-spin rounded-full h-4 w-4 border-2 border-current mr-2" />
                      Creando...
                    </>
                  ) : (
                    <>
                      <span className="mr-2">{template.icon}</span>
                      Usar Plantilla
                    </>
                  )}
                </Button>
              )}
            </CardContent>

            {/* Indicador de selecci√≥n */}
            {selectedTemplate === type && (
              <div className="absolute top-2 right-2">
                <div className="bg-blue-500 text-white rounded-full p-1">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                    className="w-4 h-4"
                  >
                    <path
                      fillRule="evenodd"
                      d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                      clipRule="evenodd"
                    />
                  </svg>
                </div>
              </div>
            )}

            {/* Etiqueta de urgencia */}
            {template.urgencyLevel === 'high' && (
              <div className="absolute top-2 left-2">
                <div className="bg-red-500 text-white text-xs px-2 py-1 rounded-full animate-pulse">
                  Urgente
                </div>
              </div>
            )}
          </Card>
        ))}
      </div>

      <div className="text-center text-sm text-muted-foreground">
        <p>Selecciona una plantilla para crear un nuevo enlace de rastreo</p>
      </div>
    </div>
  )
}
